import {Component, OnInit} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {SessionService} from '../session.service';
import {NavController} from '@ionic/angular';

@Component({
  selector: 'app-register',
  templateUrl: './register.page.html',
  styleUrls: ['./register.page.scss'],
})
export class RegisterPage implements OnInit {
  public person: { email: string, firstName: string, lastName: string, password: string, password2: string };

  constructor(private http: HttpClient, private session: SessionService, private navCtrl: NavController) {
    this.person = {email: '', password: '', password2: '', firstName: '', lastName: ''};
  }

  ngOnInit() {
  }

  async register() {
    if (this.person.password !== this.person.password2) {
      return window.alert('Passwords don\'t match!');
    }
    const loginResult = await this.http.post(this.session.apiUrl + 'api/Person', this.person).toPromise();
    console.log('registerResult: ', loginResult);
    return this.navCtrl.navigateForward('/login');
  }
}
